.login {
  width: var(--fixed-width);
  padding: 44px 15px 0 15px;
  display: flex;
  flex-direction: column;
  align-items: center;

  &__heading {
    margin-top: 25px;
    margin-bottom: 6px;
  }

  &__instruction {
    text-align: center;
    color: rgb(var(--black-4-color));
    margin-bottom: 29px;
  }

  &__input {
    width: 100%;
  }

  &__button {
    width: 100%;
    margin-top: 33px;
  }
}
