.dashboard {
  width: var(--fixed-width);
  background-color: rgb(var(--gray-8-color));
  padding: 20px 10px 70px 10px;

  &__container {
    background-color: rgb(var(--white-color));
    border-radius: 5px;
    padding: 25px 18px 32px 18px;
  }

  &__credit-score {
    margin-top: 17px;

    &:first-of-type {
      margin-top: 11px;
    }
  }
}
